import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Framework from '@/components/Framework'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/framework',
      name: 'Framework',
      component: Framework
    }
  ]
})



